import React, {useState, useEffect} from 'react';
import imageService from '../../services/imageService';
import {Loading, Paginate} from '../commons';
import {FullScreenImage, GalleryShow} from '../elements';

const HairModels = (props) => {
    const albumPerPage = 3;

    const [albums, setAlbums] = useState([]);
    const [isShowImage, setShowImage] = useState(false);
    const [albumIndex, setAlbumIndex] = useState(0);
    const [currentPage, setPage] = useState(1);

    useEffect(() => {
        imageService.getHairAlbums()
            .then(result => {
                setAlbums(result.data);
            })
            .catch(error => {
                console.log('getHairImages error', error);
            });
    }, []);

    const renderPages = () => {
        const pageNum = Math.ceil(albums.length / albumPerPage);
        const pages = [];
        
        for (let i = 0; i < pageNum; ++i) {
            const startPos = i * albumPerPage;
            pages.push(<GalleryShow show={currentPage === i + 1 ? true : false}
                key={i}
                albums={albums.slice(startPos, startPos + albumPerPage)}
                onClick={(e, i) => {
                    setAlbumIndex(startPos + i);
                    setShowImage(true);
                }}/>);
        }

        return pages;
    }

    return (
        <div id='hair-model' className='container'>
            {albums.length > 0 && <FullScreenImage index={0} images={albums[albumIndex].images} show={isShowImage} toggleFunction={() => {setShowImage(false)}}/>}
            {albums.length === 0
            ? <Loading src={require('../../assets/media/images/loading.svg')}/>
            : <div className="hair-gallery text-quite-medium">
                <h1>Mẫu tóc</h1>
                {renderPages()}
                <Paginate numPages={Math.ceil(albums.length / albumPerPage)} onClick={(i) => setPage(i)}/>
            </div>}
        </div>
    );
}

export default HairModels;