import React from 'react';

const Footer = (props) => {
    return (
        <div id={props.id} className={`footer ${props.className}`}>
            <div className="brand text-medium">
                <div className="business text-nowrap text-green">Hair Salon</div>
                <div className="name text-nowrap text-green">Huỳnh Phan</div>
            </div>
            <div className="contact">
                <div className="info">
                    <div className="about text-nowrap">Địa chỉ:</div>
                    <div className="fact">Cầu số 7 - Đào Tấn - P. Nhơn Phú - TP. Quy Nhơn</div>
                </div>
                <div className="info">
                    <div className="about text-nowrap">Điện thoại/Zalo:</div>
                    <div className="fact text-nowrap">0332 911 258</div>
                </div>
            </div>
            <div className="map">
                <iframe className="map-embeded" title="map-embeded"
                    src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d1169.1682303031384!2d109.18167019399611!3d13.807475436749211!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x316f14b42bac5151%3A0x649d75a185eba56e!2zQ2FmZSBD4bqndSBT4buRIDcu!5e1!3m2!1sen!2s!4v1579787839563!5m2!1sen!2s"
                    frameBorder="0" />
            </div>
        </div>
    );
}

export default Footer;