import React, {useState} from 'react';

const MenuButton = (props) => {
    const [isOpen, setOpen] = useState(false);

    const buttonStyle = {
        width: props.width,
        height: props.height
    };

    const stickStyle = {
        backgroundColor: props.color
    };

    const onClickHandle = () => {
        setOpen(!isOpen);
        props.onClick();
    };

    return <div className={`menu-button ${isOpen ? 'open' : ''}`}
        onClick={onClickHandle}
        style={buttonStyle}>
            <div className="stick stick-top" style={stickStyle}/>
            <div className="stick stick-mid" style={stickStyle}/>
            <div className="stick stick-bot" style={stickStyle}/>
        </div>
}

export default MenuButton;