import axios from 'axios';

export const get = (url, params = null, headers = null, withCredentials = false) => {
    return axios.get(url, {params: params, headers: headers, withCredentials: withCredentials});
}

export const post = (url, data, headers = null, withCredentials = false) => {
    return axios.post(url, data, {headers: headers, withCredentials: withCredentials});
}

export default {get, post};