import React, {useState, useEffect, useRef} from 'react';

const GalleryShow = (props) => {
    const imageRef = useRef([]);
    const [isShow, setVisibility] = useState(false);

    useEffect(() => {
        setVisibility(props.show);
    }, [props.show]);

    useEffect(() => {
        if (isShow && props.albums) {
            for (let i = 0; i < props.albums.length; ++i)
                imageRef.current[i].src = imageRef.current[i].dataset.src;
        }
    }, [isShow, props.albums]);

    if (!isShow || !props.albums)
        return null;

    return (
        <div className='gallery-show'>
            {props.albums && props.albums.map((album, i) => (
            <div className="img-holder" key={i} onClick={(e) => props.onClick(e, i)}>
                <div className="img-mask">
                    <p className="img-name text-quite-small">{album.name}</p>
                    <p className="img-desc text-small">{album.description}</p>
                    <p className="img-price text-small">{album.price}{album.price ? 'k' : ''}</p>
                </div>
                <img ref={el => imageRef.current[i] = el} alt={album.description} key={i}
                    data-src={album.images[0].link}
                    src={props.placeHolder} />
            </div>))}
        </div>
    );
}

export default GalleryShow;