import React from 'react';

class Introduction extends React.Component {
    render() {
        return <div className="introduction">
            <div className="brand">
                <div className="text-bold text-large text-green">HUỲNH PHAN</div>
                <div className="text-quite-medium">Giờ hoạt động: 9h - 19h</div>
            </div>
            <div className="more-info">
                <div className="intro half">
                    <div className="text-medium"><span className="text-bold text-quite-large">CHUYÊN</span> cắt tóc <span className="text-bold text-green text-nowrap">Nam, Nữ</span></div>
                    <div className="text-bold text-medium">UỐN</div>
                    <div className="text-bold text-medium">DUỖI</div>
                    <div className="text-bold text-medium">NHUỘM</div>
                    <div className="text-bold text-medium">PHỤC HỒI</div>
                </div>
                <div className="illustrate half">
                    <img src={require('../../assets/media/images/scissors.jpg')} alt="scissors"/>
                </div>
            </div>
        </div>
    }
}

export default Introduction;