import React, {useState, useEffect} from 'react';
import servicesService from '../../services/servicesService';
import {Loading} from '../commons';

const Price = (props) => {
    const [service, setService] = useState([]);

    useEffect(() => {
        servicesService.getAllServices()
            .then(res => {
                setService(res.data);
            })
            .catch(error => {
                console.log('getServices', error);
            });
    }, []);

    const renderServiceList = (serviceList) => {
        if (serviceList.length === 0)
            return null;

        return <ul>
            {serviceList.map(service => (
                <li key={service._id}>
                    <p><span>{service.name}</span> <span className="text-nowrap"><span>{service.min_price}</span>{service.min_price && 'k'} {service.max_price && '-'} <span>{service.max_price}</span>{service.max_price && 'k'}</span></p>
                    {service.children && renderServiceList(service.children)}
                </li>
            ))}
        </ul>
    }

    return (
        <div id='price'>
            {service.length === 0
            ? <Loading src={require('../../assets/media/images/loading.svg')}/>
            : <div className="price-content text-quite-medium">
                <h1>Dịch vụ</h1>
                {renderServiceList(service)}
            </div>}
        </div>
    );
}

export default Price;