import React from 'react';
import {Header, Footer, ScrollTopButton} from '../components/commons';
import {menuItems} from '../constants/data';
import _ from 'lodash';

class Layout extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isShowTopButton: false
        }
    }

    onScrollHandler = () => {
        if (document.documentElement.scrollTop > 80) {
            if (this.state.isShowTopButton === false)
                this.setState({isShowTopButton: true});
        } else {
            if (this.state.isShowTopButton === true)
                this.setState({isShowTopButton: false});
        }
    }

    componentDidMount() {
        this.debouceOnScrollHandler = _.debounce(this.onScrollHandler, 100);
        window.addEventListener('scroll', this.debouceOnScrollHandler);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.debouceOnScrollHandler);
    }

    renderMain() {}

    render() {
        return <div className="layout">
            <img className="drowned-background" src={require('../assets/media/images/Subtle-Prism.svg')} alt="Drowned background"/>
            <Header className="container" collapseWidth={1024} menuItems={menuItems}/>
            {this.renderMain()}
            <Footer id="contact" className="container"/>
            {this.state.isShowTopButton && <ScrollTopButton />}
        </div>
    }
}

export default Layout;