import React, {useState} from 'react';

const Paginate = (props) => {
    const [chosenPage, setPage] = useState(1);

    const onPageClick = (e, n, i) => {
        if (chosenPage !== i && i >= 1 && i <= n) {
            setPage(i);
            if (typeof props.onClick === 'function')
                props.onClick(i);
        }
    }

    const getPageRange = (page, n) => {
        let start = page === n ? page - 2 : page - 1;
        let end = start < 1 ? 3 : start + 2;

        if (start <= 1)
            start = 2;
        if (end >= n)
            end = n - 1;
        
        return [start, end];
    }

    const renderPage = (page, n) => {
        return <div className={`page ${page === chosenPage ? 'chosen' : ''}`} key={page} onClick={(e) => onPageClick(e, n, page)}><span>{page}</span></div>;
    }

    const renderPaginate = (n) => {
        const range = getPageRange(chosenPage, n);

        const paginate = [];
        paginate.push(<div className="left page" key="left" onClick={(e) => onPageClick(e, n, chosenPage - 1)}>&#10094;</div>)
        if (n > 0)
            paginate.push(renderPage(1, n));
        if (range[0] > 2)
            paginate.push(<span key="left-dot">...</span>)
        for (let i = range[0]; i <= range[1]; ++i) {
            paginate.push(renderPage(i, n));
        }
        if (range[1] < n - 1)
            paginate.push(<span key="right-dot">...</span>);
        if (n > 1)
            paginate.push(renderPage(n, n));
        paginate.push(<div className="right page" key="right" onClick={(e) => onPageClick(e, n, chosenPage + 1)}>&#10095;</div>);

        return paginate;
    }

    return (
        <div className='paginate'>
            {renderPaginate(props.numPages)}
        </div>
    );
}

export default Paginate;