import React, {useState, useEffect} from 'react';
import MenuButton from '../elements/MenuButton';
import _ from 'lodash';

const Header = (props) => {
    const [isInCollapseMode, setInCollapseMode] = useState(false);
    const [isCollapsed, setCollapsed] = useState(false);
    useEffect(() => {
        const mql = window.matchMedia(`(max-width: ${props.collapseWidth}px)`);

        function screenTest(e) {
            if (e.matches) {
                /* the viewport is collapseWidth pixels wide or less */
                setInCollapseMode(true);
                setCollapsed(false);
            } else {
                setInCollapseMode(false);
            }
        }
        screenTest(mql);

        mql.addListener(screenTest);

        return () => {
            mql.removeListener(screenTest);
        };
    }, [isInCollapseMode, props.collapseWidth]);

    const [isScrolledDown, setScrolledDown] = useState(false);
    useEffect(() => {
        function onScrollChangeHeader() {
            if (document.body.scrollTop > 0 || document.documentElement.scrollTop > 0) {
                setScrolledDown(true);
            } else {
                setScrolledDown(false);
            }
        }
        onScrollChangeHeader();

        const throttleVer = _.throttle(onScrollChangeHeader, 100, true);
        
        window.addEventListener('scroll', throttleVer);

        return () => {
            window.removeEventListener('scroll', throttleVer);
        };
    }, [isScrolledDown]);

    const onMenuItemClick = (e) => {
        e.preventDefault();

        const element = window.document.getElementById(e.target.href.split('#').pop());
        if (element) {
            const bodyTop = document.body.getBoundingClientRect().top;
            const elementTop = element.getBoundingClientRect().top;
            window.scrollTo({
                top: elementTop - bodyTop - 80,
                behavior: "smooth"
            })
        }
        else
            alert('Not yet!');
    }

    return (
        <div id='header' className={`header ${isInCollapseMode ? 'collapse' : ''} ${props.className} ${isScrolledDown ? 'white-header' : ''}`}>
            <div className="main-logo">
                <div className="logo"><a href="/">Hair Salon</a></div>
            </div>
            <div className={`main-navigation ${isInCollapseMode && !isCollapsed ? 'menu-hide' : ''}`}>
                {props.menuItems.map((item, index) => {
                        return <a href={item.uri} className="menu-item" key={index} onClick={onMenuItemClick}>{item.name}</a>;
                    })}
            </div>
            <div className="main-nothing">
                {isInCollapseMode && <MenuButton onClick={() => setCollapsed(!isCollapsed)}
                    width={24} height={20} color={'#03fc0b'}/>}
            </div>
        </div>
    );
}

export default Header;