import React from 'react';
import Layout from '../templates/Layout';
import {Introduction, Price, HairModels, Products} from '../components/elements';

class Landing extends Layout {
    constructor(props) {
        super(props);
        this.state = {
            ...this.state
        }
    }

    renderMain() {
        return (
            <React.Fragment>
                <Introduction />
                <Price />
                <HairModels />
                <Products />
            </React.Fragment>
        );
    }
}

export default Landing;