import React from 'react';
import Icon from './Icon';

const ScrollTopButton = (props) => {
    const scrollToTop = () => {
        window.scrollTo({
            top: 0,
            left: 0,
            behavior: 'smooth'
        });
    }

    return <div className='scroll-top-button' onClick={scrollToTop}>
        <Icon src={require('../../assets/media/icons/up-arrow.svg')} size={24}/>
    </div>
}

export default ScrollTopButton;