import React, {useState, useEffect, useRef} from 'react';

const FullScreenImage = (props) => {
    const thumbnailW = 50;
    const thumbnailH = 40;

    const imageRef = useRef([]);
    const [index, setIndex] = useState(0);
    const [isShow, setVisibility] = useState(false);

    useEffect(() => {
        setVisibility(props.show);
        if (props.show && !isNaN(props.index))
            setIndex(props.index);
    }, [props.show, props.index]);

    useEffect(() => {
        if (isShow) {
            document.addEventListener("keydown", handleKeyDown);

            return () => {
                document.removeEventListener("keydown", handleKeyDown);
            }
        }
    }, [isShow]);

    const onBtnClick = (change) => {
        setIndex(index => {
            let i = index + change;
            if (i < 0)
                i = props.images.length - 1;
            if (i >= props.images.length)
                i = 0;

            imageRef.current[i].src = imageRef.current[i].dataset.src;
            return i;
        });
    }

    const handleKeyDown = (event) => {
        switch (event.keyCode) {
            case 37:
                onBtnClick(-1);
                break;
            case 39:
                onBtnClick(1);
                break;
            case 27:
                setVisibility(false);
                props.toggleFunction();
                break;
        }
    }

    const renderImages = () => {
        return props.images.map((image, i) => (
            <img ref={el => imageRef.current[i] = el}
                alt={image.description} key={i}
                style={i !== index ? {display: "none"} : {}}
                data-src={image.link}
                src={i === index ? image.link : ''}/>
        ));
    }

    const renderThumbnails = () => {
        return (
            <div className="thumbnails" style={{
                    transform: `translateX(${thumbnailW*(props.images.length/2 - index) - thumbnailW/2}px)`
                }}>
                {props.images.map((image, i) => (
                <img className="thumbnail-img"
                    alt={image.description} key={i}
                    style={index === i ? {
                        width: thumbnailW + 10,
                        height: thumbnailH + 10,
                        transform: 'none',
                        cursor: 'initial'} : {}}
                    src={image.link}
                    onClick={() => setIndex(i)}/>
                ))}
            </div>
        );
    }

    if (!isShow || !props.images || props.images.length === 0)
        return null;

    return (
        <div className='fullscreen-image' onClick={(e) => {
                if (e.target.className === 'fullscreen-image') {
                    setVisibility(false);
                    props.toggleFunction();
                }
            }}>
            <div className="left-button change-button" onClick={() => onBtnClick(-1)}><span>&#10094;</span></div>
            {renderImages()}
            {renderThumbnails()}          
            <div className="right-button change-button" onClick={() => onBtnClick(1)}><span>&#10095;</span></div>
        </div>
    );
}

export default FullScreenImage;